import { MyLoading, MyCard } from "@macashipo/mlib"
import React from "react"
import { fnListMyPage, KConfigPagePage } from "src/helpers/fnList"
import styled from "styled-components"

const WrapGrid = styled.div`
  display: grid;
  grid-gap: 10px;
  & > div {
    margin: 0px;
  }
`
class MUIATTableGridCard extends React.Component {
  getData = () => {
    const { cfp } = this.props
    return fnListMyPage.getDataList(cfp.fnList, [])
  }
  getShowPagination = () => {
    const { cfp } = this.props
    const { onePage } = cfp
    if (onePage["UI_IsInDetail"] === true) {
      return false
    }
    return true
  }
  _renderList = () => {
    const { cfp, data } = this.props
    let _ui = []
    let _data = data || this.getData() //uu tien lay data tu ngoai truyen vao
    console.warn("render List", _data)
    let _configCard =
      fnListMyPage.fnGetConfigPagePage(cfp.fnList, [
        KConfigPagePage.configCard,
      ]) || {}
    if (_data && _data.length > 0) {
      _ui = _data.map((v, i) => {
        return (
          <MyCard
            key={v.Id || i}
            type={_configCard.type}
            config={_configCard}
            data={v}
            fnList={cfp.fnList}
          />
        )
      })
    }
    return (
      <WrapGrid
        style={{
          gridTemplateRows: "auto",
          gridTemplateColumns: "repeat( auto-fit, minmax(300px, 1fr) )",
        }}
      >
        {_ui}
      </WrapGrid>
    )
  }
  render() {
    const { cfp } = this.props
    if (cfp) {
      return <div className="mpat-table-grid mb-3">{this._renderList()}</div>
    } else {
      return (
        <div
          style={{
            width: "100%",
            textAlign: "center",
          }}
        >
          <MyLoading />
        </div>
      )
    }
  }
}

export default MUIATTableGridCard
